import mEcharts from '../components/Echarts'

export default {
  name: 'Diagram',
  components: {
    mEcharts
  },
  data(){
    return {
      a:'水果销售统计',
      b:'销售数量',
      c:['香蕉','苹果','橘子'],
      d:'销售统计',
      e:[
          {value:3, name:'香蕉'},
          {value:3, name:'苹果'},
          {value:3, name:'橘子'}
        ],
      s: {
        height: ''
      }
    }
  },
  created(){
    // 获取屏幕高度
    this.s.height = document.documentElement.clientHeight - 44 + 'px';
  },
}



