import mScroller from '../components/Scroller'
import mLeftSlider from '../components/LeftSlider'

export default {
  components: {
    mScroller,
    mLeftSlider,
  },
  data() {
    return {
      marginTop:'margin-top:40px;',
      pageStart: 0, // 开始页数
      pageEnd: 0, // 结束页数
      listdata: [], // 数据列表
      scrollData:{
        noFlag: false //暂无更多数据显示
      }
    }
  },
  mounted: function() {
    // 首次请求数据
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.axios.get('/api/testData').then((response) => {
        this.listdata = response.data.data.list;
        // 获取总页数
        this.pageEnd = response.data.data.totalPage;
        // 还原
        this.pageStart = 0;
      })
    },
    getMsgFromChild (v) {
      this.listdata.splice(v,1)
    },
    // 下拉刷新
    onRefresh(done) {
      this.fetchData();
      done(); // call done
    },
    // 上拉加载更多
    onInfinite(done) {
      this.pageStart++;
      // 加载条
      let more = this.$el.querySelector('.load-more');
      // 判断是否显示加载条
      if(this.pageStart > this.pageEnd){
        //走完数据调用方法
        this.scrollData.noFlag = true;
      }else{
        let _this = this;
        this.axios.get('/api/testData').then((response) => {
          _this.listdata = _this.listdata.concat(response.data.data.list);
          // 获取总页数
          _this.pageEnd = response.data.data.totalPage;
        })
      }
      // 隐藏加载条
      more.style.display = 'none';
      done();
    }
  }
}



