export default {
  name: 'Toast',
  data(){
    return {
      //
    }
  },
  methods:{
    openTop(){
      this.$toast.top('top');
    },
    openCenter(){
      this.$toast.center('center');
    },
    openBottom(){
      this.$toast('bottom');  // or this.$toast.bottom('bottom');
    },
    openLoading(){
      this.$loading('loading...');
      let self = this;
      setTimeout(function () {
        self.closeLoading()
      }, 2000)
    },
    closeLoading(){
      this.$loading.close();
    }
  }
}



