export default{
  props: {
    id:{
      type:String
    },
    isRouter:{
      type:Boolean,
      default:false
    }
  },
  computed: {
    isActive(){
      if(this.$parent.value===this.id){
        return true;
      }
    }
  },
  methods:{
    goToRouter(){
      this.$parent.$emit('input',this.id)
      //判断是否为路由跳转
      if(this.isRouter){
        //根据id跳转到对应的路由页面
        this.$router.push(this.id)
      }
    }
  }
}



