import QRCode from '../components/QRCode.vue'
import { MessageBox } from 'mint-ui';

export default {
  name: 'QR',
  components: {
    QRCode
  },
  data(){
    return {
      val:'https://www.baidu.com/s?wd=123'
    }
  },
  methods: {
    changeUrl(){
      MessageBox.prompt('请输入新的url').then(({ value, action }) => {
        this.val = value;
      });
    }
  }
}



